        if (user.value.discord || user.value.email || user.value.telegram) {
            console.log()
            addUser()
            dialogue.value = true
            selectedReward.value = null
            user.value = {
                video: null,
                name: null,
                discord: null,
                email: null,
                telegram: null,
                reward: computed(() => selectedReward.value),
                topics: null,
                questions: null,
            }
        } else {
            alert('Please choose at least 1 of the preferred way to communicate.')
        }
    }
}
const requiredField = [
    (value) => {
        if (value) return true;
        return "Please provide information for this field";
